/* harmony default export */ __webpack_exports__["a"] = ({payoffs:{visual:'Show, don\'t yell',specialist:'U heeft een goed verhaal, maar een specialist moet dat er eens uittrekken?'},actions:{contact:'Neem contact op'},contact:{visual:'De koffie staat klaar'},intro:'U heeft zoveel te vertellen, maar het komt er niet van.\n        Door een gebrek aan tijd, creativiteit of specialiteit.\n        Uw verhaal is het delen waard, maar hoe?\n        Anderen doen dat beter.\n        Met blogs, artikelen, persberichten.\n        Noem maar op.\n        Dat wilt u veranderen. En daar zijn wij voor.',how:{heading:'U heeft een goed verhaal, maar een specialist moet dat er eens uittrekken?',body:'\n# Hoe?\nMet merkverhalen, blogs, artikelen, nieuwsbrieven, speeches, sfeerverslagen, persberichten, webteksten en human interestverhalen.\nEn dat is nog maar een greep uit de mogelijkheden om uw organisatie op het netvlies te krijgen.\n\n## Uw verhaal (weer) tot leven brengen\nDe zeggingskracht van verhalen is de sleutel tot (zakelijk) succes van organisaties.\nVanuit die filosofie maakt en verspreidt True Story relevante en betekenisvolle content, die naadloos aansluit op de bedrijfsstrategie.\nDat betekent een combinatie van schrijfvaardigheid en verbeeldingskracht om verhalen van organisaties (weer) tot leven te brengen, nieuw leven in te blazen en wereldkundig te maken.\n\n## Met wie?\nDe waarheid is dat ik niet alles kan. Lang niet alles. Zou ik ook niet willen. Het is natuurlijk goed om je,\nook als schrijver, breed te ontwikkelen, maar dat betekent niet dat je alles moet beheersen. Dat kan\nniet en dat hoeft niet. Belangrijker is de juiste mensen om je heen verzamelen. Professionals die je\nzonder aarzelen naar voren schuift. En waar je zelf ook graag mee aan tafel zit, want je bent pas\ngoede partners als je ook kunt lachen met elkaar. Net als thuis, toch?\n\nTrue Story Communicatie staat dan ook in nauw contact met diverse vormgever, dtp\u2019ers, filmmakers,\nfotografen, tekstschrijvers, redacteuren/correctoren en andere communicatiebureaus.\n\nOmgeven door deze creatieve en ervaren vakmensen kan True Story Communicatie met elk\ncommunicatievraagstuk uit de voeten\n    '},what:{heading:'Wat wij doen?',intro:'True Story neemt schrijf- en redactiewerk uit handen, verhoogt de kwaliteit van tekstproducties en biedt toegevoegde waarde door ons oog voor het bijzondere verhaal.'},philosophy:{heading:'True Story | Communicatie is een bureau voor communicatie en public relations',body:'Gestoeld op de kracht van verhalen, gespecialiseerd in tekst en redactie.\n          Teksten om te verleiden, informeren of entertainen.\n          Door hoofden te bereiken en harten te raken.\n\n          En dat alles onder het motto: Show, don\u2019t yell\n\n          Want schreeuwen is niet nodig om gezien te worden.\n          U moet gewoon de juiste dingen zeggen, op het juiste moment en op de juiste plek.\n\n        '},products:[{name:'ghostwriting',heading:'Ghostwriting',body:'Graag een blog of artikel schrijven, maar komt u er niet aan toe?\n      Wilt u na een kwartiertje bellen een tekst onder ogen krijgen die u zelf geschreven had kunnen hebben? Wij zijn gespecialiseerd in ghostwriterschap.'},{name:'strategy',heading:'Verhalen en strategie',body:'Storytelling, content-/en communicatiestrategie en tekstadvies, visiestukken, speeches, tekstadvies, contentstrategie, scenario\u2019s en scripts voor videoproducties.'},{name:'writing',heading:'Tekstschrijven',body:'Copywriting en bedrijfsjournalistiek; bedrijfsverhalen, artikelen, e-mailings, nieuwsbrieven, brochures, persberichten, reportages, whitepapers, testimonials, speeches, SEO-teksten, AdWords, short copy, proza (fictie &amp; non-fictie).'},{name:'editing',heading:'Redactie/correctie',body:'Tekstredactie, webredactie, hoofd- en eindredactie, herschrijven, tekstcorrectie, jaarverslagen, columns, voor- en nawoorden.'}],about:{heading:'Over de oprichter/eigenaar',body:'\n# Over de oprichter/eigenaar\nRick van Leeuwen is een verhalenschrijver met een achtergrond als allround tekstschrijver,\n(bedrijfs)journalist, copywriter \xE9n prozaschrijver. Een verhalenschrijver met journalistieke inborst, die\ninbeeldingsvermogen en schrijfvaardigheid aan elkaar verbindt. Als allround tekstschrijver werkte hij\nruim 10 jaar voor een toonaangevend pr-bureau.\n\nAls prozaschrijver publiceerde hij een roman en talloze korte verhalen, wat alles zegt over zijn passie\nvoor storytelling. Hij kan als geen ander in de huid kruipen van een personage en heeft de\nverteltechnieken in huis om in de taal van dat personage de gewenste doelgroep aan te spreken. Als\neen kameleon kan hij elke gedaante aannemen in alle omgevingen.\n\n## Over True Story\nDe naam True story is ontstaan vanuit de gedachte dat storytelling pas werkt als organisaties hun\nechte verhaal vertellen. Dat is belangrijk om geloofwaardig over te komen en mensen voor je te\nwinnen. De term truthtelling is eigenlijk beter op zijn plaats dan storytelling.\n\nTrue story helpt organisaties op freelancebasis bij het vaststellen, vormgeven en funderen van een\novertuigende bedrijfsidentiteit. Door het cre\xEBren en verspreiden van relevante, betekenisvolle content\nbrengen wij het verhaal van organisaties tot leven, of blazen het nieuw leven in, en maken het verhaal\nwereldkundig. Vanuit de overtuiging dat de zeggingskracht van verhalen bedrijven laat groeien, dat de\nkracht van content zakelijk succes verhoogt. Noem het gerust verhalenmarketing. Noem het\ntruthtelling.\n      ',link:'http://',action:'Bekijk dit project',visual:'http://',tags:['ghostwriting']}});

